#list{
   height: auto;
   width: 77%;
   background-color:white;
   margin-left: 11.5%;
   margin-top: 30px;
   padding-bottom: 30px;
   border: 2px solid rgb(155, 248, 143);
}
#list>h1{
   color: white;
   width: 100%;
   height: 40px;
   /* background-color: rgb(62, 224, 189); */
   background-color: cadetblue;
   padding-left: 41%;
}
#list>h3{
   color: black;
   margin-top: 30px;
   background-color: rgb(155, 248, 143);
   padding-left: 34%;
   margin-bottom: 30px;
}
#list>li{
   padding-left: 55px;
   margin-top: 10px;
}
.a{
   color:  blue;
   font-size: large;
   font-weight: bolder;
}
.a:hover{
   color: black;
}
#page{
   width: 77.5%;
   margin-top: 4%;
   margin-left: 11.5%;
}
#page>h1{
   color: blue;
   text-align: center;
   padding-bottom: 40px;
}
#page>h2{
   padding-bottom: 5px;
}
#page>div{
   border: 1px solid black;
}
#aa{
   display: flex;
   justify-content: center;
   margin: 60px;
   height: auto;
}
#aa>h1{
   color: red;
   font-size: xxx-large;
}
#p1{
   padding: 30px;
   text-align: center;
   width: 100%;
   height: auto;
   border: 1px solid black;
}
#p1>h1{
   color: blue;
}
#p1>h2{
   padding-top: 10px;
   color: rgb(255, 21, 204);
}
#p1>h3{
   padding-top: 10px;
   color: green;
}
#p2{
   font-size: large;
   color: red;
   width: 100%;
   padding: 20px;
   text-align: center;
   border: 1px solid black;
}
#p3{
   width: 100%;
   height: auto;
   display: flex;
}
#p3>div{
   width: 50%;
   border: 1px solid black;
   padding-bottom: 30px;
}
#p3>div>h1{
   text-align: center;
   padding-top: 20px;
   padding-bottom: 20px;
   color: green;
}
#p3>div>p{
  padding-top: 5px;
  padding-left: 35px;
  font-size: x-large;
}
#p4{
   border: 1px solid black;
   padding: 35px;
   width: 100%;
   height: auto;
   
}
#p4>h1{
   color: rgb(255, 21, 204);
   padding-bottom: 10px;
}
#p4>h3{
   padding-top: 10px;
   padding-left: 60px;
}
#p5>h1{
   color: blue;
   text-align: center;
   padding: 25px;
   border: 1px solid black;
}
#p5>div{
   padding: 35px;
   border: 1px solid black;
}
#p5>div>h1{
   color: rgb(255, 21, 204);
}
#p5>div>h2{
  padding-top: 35px;
   color: green;
   padding-bottom: 10px;
}
#p5>div>h3{
   padding-top: 7px;
   padding-left:30%;
}
#p6{
   text-align: center;
   color: red;
   border: 1px solid black;
   padding: 25px;
}
#p7{
   border: 1px solid black;
   padding: 35px;  
}
#p7>h1{
   color: rgb(255, 21, 204);
   padding-bottom: 10px;
}
#p7>h3{
   padding-top: 5px;
   padding-left: 30%;
}
#p8>div{
   text-align: center;
   display: flex;
   justify-content: center;
   padding: 25px;
   border: 1px solid black;
}
#p9>div{
   display: flex;
}
#p9>div>div{
   display: flex;
   justify-content: center;
   width: 100%;
   padding: 25px;
   border: 1px solid black;
}
#p9>div>div>h2{
   font-size: 30px;
   color: red;
}
#p9>div>div>a{
   color: blue;
}
#ins{
   text-align: center;
   margin: 50px;
}
#ins>h1{
   padding-bottom: 20px;
   color: red;
   font-size: 40px;
   text-align: center;
}
@media only screen and (max-width: 723px) {
   #list{
       margin-left: 0;
       margin-top: 30px;
       width: 100%;
    }
    #list>li{
       padding-left: 20px;
    }
    #list>h3{
       padding-left: 14%;
    }
    #list>h1{
       padding-top: 2px;
       font-size: 29px;
       padding-left: 32%;
    }
    .a{
       font-size: 14px;
    }
    
    #page>h1{
       padding-top: 10px;
       padding-bottom: 20px;
       padding-left: 0px;
       text-align: center;
       font-size: 15px;
    }
