    }
    #p3>div>p{
       padding-left: 5%;
       font-size: 10px;
    }
    #p4{
       padding: 15px;
       font-size: 8px;
    }
    #p5>h1{
       font-size: 10px;
       padding: 10px;
    }
    #p5>div{
       font-size: 8px;
       padding: 15px;
    }
    #p5>div>h2{
     padding-top: 12px;
     padding-bottom: 5px;
    }
    #p5>div>h3{
     padding-top: 5px;
       padding-left: 20%;
    }
    #p6{
       padding: 10px;
       font-size: 8px;
    }
    #p7{
       padding: 15px;
       font-size: 8px;
    }
    #p7>h3{
       padding-left: 20%;
    }
    #p8>div{
       padding: 13px;
       font-size: 9px;
    }
    #p9>div>div>h2{
       font-size: 17px;
    }
    #p9>div>div{
       text-align: center;
       padding: 12px;
       font-size: 10px;
    }
    #ins{
       margin-top: 15px;
       font-size: 10px;

    }
    #ins>h1{
     padding: 10px;
     text-align: center;
     font-size: 20px;
    }
}

